import * as React from "react";
import * as ReactDOM from "react-dom";
import { JSWindow, ListView } from "@jswf/react";

function App() {
  let listViewRef = React.useRef<ListView>(null);
  const [message, setMessage] = React.useState("");
    let count = 1;
  return (
    <>
      <JSWindow title="" x={0} y={0}>
        <button
          onClick={() => {
            //
            listViewRef.current!.addItem([count++, "<br/>", 0, 1]);
            listViewRef.current!.addItem([count++, <><br/></>, 0, 1]);
          }}
        >
          
        </button>
        <button
          onClick={() => {
            const items = listViewRef.current!.getSelectItems();
            items.sort((a,b)=>b-a);
            for(const item of items)
              listViewRef.current!.removeItem(item);
          }}
        >
          
        </button>
        <br/>
        {message}
      </JSWindow>
      <JSWindow title="ListView">
        <ListView
          ref={listViewRef}
          onItemClick={(row, col) => {
            //
            const item = listViewRef.current!.getItem(row, col);
            //
            if (item) setMessage(`${item.toString()}`);
          }}
        >
          <div>
            <div data-type="number">No</div>
            <div data-width={100}><br /></div>
            <div data-type="number"></div>
            <div data-type="number"></div>
          </div>
          <div>
            <div>{count++}</div><div></div><div>5</div><div>10</div>
          </div>
          <div>
            <div>{count++}</div><div></div><div>10</div><div>40</div>
          </div>
          <div>
            <div>{count++}</div><div></div><div>18</div><div>120</div>
          </div>
          <div>
            <div>{count++}</div><div></div><div>30</div><div>380</div>
          </div>
          <div>
            <div>{count++}</div><div></div><div>40</div><div>700</div>
          </div>
        </ListView>
      </JSWindow>
    </>
  );
}

ReactDOM.render(<App />, document.getElementById("root") as HTMLElement);
